$deepGray: #282c34;
$lightGray: #888888;
$paleGray: #dcdcdc;
$clickable: #1e90ff;
$clickableHover: #4169e1;
$deepBlue: #000080;
$black: #000000;
$white: #ffffff;
$orange: #ff6347;
$green: #9acd32;
$red: #dc143c;

body {
	background: $paleGray;
	padding: 0;
	margin: 0;
	font-weight: normal;
	overflow: hidden;
}

.section {
	height: 100vh;
	width: 100vw;
	margin: 0;
	padding: 30px;
	overflow: hidden;
}

h2 {
	color: $white;
	text-align: center;
}
.center {
	margin: auto;
}
button {
	background: $black;
	padding: 10px;
	color: $white;
	transition-duration: 0.5s;
	border: none;
	letter-spacing: 1px;
	min-width: 120px;
}
button:hover {
	background: $clickableHover;
}

.startButton {
	text-transform: uppercase;
	border-radius: 10px;
	margin: 0 auto;
	display: block;
}
